import { FooterBlur } from "@/components/footer/footer-blur";
import { GithubIcon } from "@/components/footer/icons";
import Link from "next/link";

export function Footer() {
  return (
    <footer className="relative -mt-25 overflow-hidden py-12 pt-37 md:py-25 md:pt-37">
      <FooterBlur />
      <div className="mx-auto w-full max-w-6xl px-6">
        <div className="flex items-center justify-center">
          <Link
            href="https://github.com/your-username/your-repo"
            title="View our GitHub repository"
            target="_blank"
            className="inline-flex items-center gap-2 text-sm md:text-base"
          >
            <GithubIcon className="h-5 w-5" />
            <span>GitHub</span>
          </Link>
        </div>
      </div>
    </footer>
  );
}
